import React, { useEffect, useState } from "react";
import { Link, NavLink, withRouter } from "react-router-dom";
import gsap from "gsap";
import { ReactComponent as UpArrow } from "../assets/up-arrow-circle.svg";

const tl = gsap.timeline();
const Header = ({ dimension, history }) => {
  const [menuState, setMenuState] = useState({ menuOpen: false });
  useEffect(() => {
    history.listen(() => {
      setMenuState({ menuOpen: false });
    });
    if (menuState.menuOpen) {
      //  Open menu animation
      gsap.to("nav", { css: { display: "block" } });
      gsap.to("body", { css: { overflow: "hidden" } });

      tl.to(".App", {
        duration: 1,
        y: dimension.width <= 654 ? "70vh" : "80vh",
        ease: "expo.inOut",
      })
        .to(".hamburger-menu span", 0.6, {
          delay: -1,
          scaleX: 0,
          transformOrigin: "50% 0%",
          ease: "expo.inOut",
        })
        .to("#Path_1", 0.4, {
          delay: -0.6,
          css: {
            strokeDashoffset: 10,
            strokeDasharray: 5,
          },
        })
        .to("#Path_2", 0.4, {
          delay: -0.6,
          css: {
            strokeDashoffset: 10,
            strokeDasharray: 20,
          },
        })
        .to("#Line_1", 0.4, {
          delay: -0.6,
          css: {
            strokeDashoffset: 40,
            strokeDasharray: 18,
          },
        })
        .to("#circle", 0.6, {
          delay: -0.8,
          css: {
            strokeDashoffset: 0,
          },
          ease: "expo.inOut",
        })
        .to(".hamburger-menu-close", 0.6, {
          delay: -0.8,
          css: { display: "block" },
        });
    } else {
      //  Close menu animation
      tl.to(".App", 1, {
        y: 0,
        ease: "expo.inOut",
      })
        .to("#circle", 0.6, {
          delay: -0.6,
          css: {
            strokeDashoffset: -193,
            strokeDasharray: 227,
          },
        })
        .to("#Path_1", 0.4, {
          delay: -0.6,
          css: {
            strokeDashoffset: 10,
            strokeDasharray: 10,
          },
        })
        .to("#Path_2", 0.4, {
          delay: -0.6,
          css: {
            strokeDashoffset: 10,
            strokeDasharray: 10,
          },
        })
        .to("#Line_1", 0.4, {
          delay: -0.6,
          css: {
            strokeDashoffset: 40,
            strokeDasharray: 40,
          },
        })
        .to(".hamburger-menu span", 0.6, {
          delay: -0.6,
          scaleX: 1,
          transformOrigin: "50% 0%",
          ease: "expo.inOut",
        })
        .to(".hamburger-menu-close", 0, {
          delay: -0.1,
          css: { display: "none" },
        })
        .to("nav", 0, {
          css: { display: "none" },
        });
    }
  }, [menuState.menuOpen]);
  return (
    <div className="header">
      <div className="container">
        <div className="row v-center space-between">
          <div className="logo">
            <NavLink to="/">AGENCY.</NavLink>
          </div>
          <div className="nav-toggle">
            <div
              className="hamburger-menu"
              onClick={() => setMenuState({ menuOpen: true })}
            >
              <span />
              <span />
            </div>
            <div
              className="hamburger-menu-close"
              onClick={() => setMenuState({ menuOpen: false })}
            >
              <UpArrow />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default withRouter(Header);
